import { Injectable } from '@angular/core';import { Router, CanActivate } from '@angular/router';import { DataService } from './data.service';@Injectable()export class AuthGuard implements CanActivate {  constructor(    private router: Router,    public dataService: DataService  ) { }  canActivate() {    if (!this.dataService.images) {      this.router.navigate(['/']);      return false;    }    return true;  }}


