import { Component, Input } from '@angular/core';import { Router } from '@angular/router';import { DataService } from '../data.service';@Component({  selector: 'app-head',  template: `    <div class="head">      <div class="head__menu">        <div *ngIf="isDisableMenu">          <div class="icon icon-cog" (click)="activeMenu()"></div>          <ul class="menu-list" *ngIf="isActiveMenu">            <li (click)="uploader.click()" *ngIf="!dataService.images">              Update data              <input type="file" #uploader accept=".json" (change)="uploadFile($event)" style="display: none;">            </li>            <li (click)="sendTo()">Send to</li>          </ul>        </div>      </div>      <div class="head__logo" routerLink="/">        adani<span>imaging</span>      </div>    </div>  `,  styleUrls: ['./head.scss']})export class HeadComponent {  isActiveMenu = false;  isDisableMenu = false;  @Input() public set disableMenu(value) {    this.isDisableMenu = value;  }  constructor(    public router: Router,    public dataService: DataService  ) {}  uploadFile(e) {    this.dataService.uploadFile(e);    this.activeMenu();  }  sendTo() {    this.dataService.sendTo();    this.activeMenu();  }  activeMenu() {    if (!this.isActiveMenu) {      this.isActiveMenu = true;    } else {      this.isActiveMenu = false;    }  }}


